body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  background: #3f3f3f;
}

.Player {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	border-radius: 10px;
}

.Player h1 {
	color: #efefef;
	font-size: 48pt;
}

.hideReactPlayer {
	display: none !important;
}

.control {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
}

.showSong, .nextSong {
	display: block;
	margin-right: 20px;
	font-weight: 300;
	padding: 25px;
	margin-bottom: 25px;
	border: transparent;
	border-radius: 50px;
	background: #1e3c72;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #2a5298, #1e3c72);  /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, left top, right top, from(#2a5298), to(#1e3c72));
	background: -webkit-linear-gradient(left, #2a5298, #1e3c72);
	background: -o-linear-gradient(left, #2a5298, #1e3c72);
	background: linear-gradient(to right, #2a5298, #1e3c72); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	color: #fff;
	font-size: 20pt;
	-webkit-transition: -webkit-box-shadow 150ms ease-in;
	transition: -webkit-box-shadow 150ms ease-in;
	-o-transition: box-shadow 150ms ease-in;
	transition: box-shadow 150ms ease-in;
	transition: box-shadow 150ms ease-in, -webkit-box-shadow 150ms ease-in;;
}

.showSong:hover, .nextSong:hover {
	-webkit-box-shadow: 0 0 9px rgba(42, 82, 152, .7);
	        box-shadow: 0 0 9px rgba(42, 82, 152, .7);
}

.showSong:focus, .showSong:active, .nextSong:focus, .nextSong:active {
	outline: none;
}


@-webkit-keyframes pulse {
  50% {
    background: white;
  }
}
@keyframes pulse {
  50% {
    background: white;
  }
}

.loading {
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 0.25rem solid rgba(255, 255, 255, 0.2);
  border-top-color: white;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}

.loading-pulse {
  position: relative;
  width: 6px;
  height: 24px;
  background: rgba(255, 255, 255, 0.2);
  -webkit-animation: pulse 750ms infinite;
          animation: pulse 750ms infinite;
  -webkit-animation-delay: 250ms;
          animation-delay: 250ms;
}
.loading-pulse:before, .loading-pulse:after {
  content: '';
  position: absolute;
  display: block;
  height: 16px;
  width: 6px;
  background: rgba(255, 255, 255, 0.2);
  top: 50%;
  -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  -webkit-animation: pulse 750ms infinite;
          animation: pulse 750ms infinite;
}
.loading-pulse:before {
  left: -12px;
}
.loading-pulse:after {
  left: 12px;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
}



